import React, { Component } from 'react'; 
//so we can abbreviate React.Component to just Component
// in the class declaration of the Page component below

import Header from '../components/Header';
import Meta from '../components/Meta';
import styled, { ThemeProvider, injectGlobal } from 'styled-components';

//NOT styled_components but rather a JS object
//we give it to <ThemeProvider> to create real CSS
//this btw is global when it is given as the value for ThemeProvider
const theme = {
  red: '#FF0000',
  black: '#393939',
  grey: '#3A3A3A',
  lightgrey: '#E1E1E1',
  offWhite: '#EDEDED',
  blue:'blue',
  maxWidth: '1000px',
  bs: '0 12px 24px 0 rgba(0, 0, 0, 0.09)',
};


const StyledPage = styled.div`
	background: white;
	color: ${props => props.theme.black};
`;

const Inner = styled.div`
	max-width: ${props => props.theme.maxWidth};
	max-width: ${props => props.theme.red};
	margin: 0 auto;
	padding 2rem;
`;

class Page extends Component {
	render() {
		return (
			<ThemeProvider theme={theme}>
				<StyledPage>
					<Meta/>
					<Header/>
					<Inner>{this.props.children}</Inner>
				</StyledPage>
			</ThemeProvider>
		);
	}
}

export default Page;